import './App.css';
import './index.css';
import Header from './header';
import Footer from './footer';
import React from 'react'
import { HashRouter, Route, Routes } from 'react-router-dom';
import Home from './page/home';
import About from './page/about';
import Contact from './page/contact';
import Blog from './page/unlocking';
import SingleBlog from './page/single-blog';
import HoneLabRed from './page/hone-lab-red';
import HoneCarbon from './page/hone-carbon';
import HoneLabBlack from './page/hone-lab-black';
import Ourteam from './page/our-team';
import BlogPage from './page/blog-page';
import StayUplated from './page/stay-uplated';
import ContactUs from './page/contact-us';
import HoneSupport from './page/hone-support';
import Documents from './page/documents';
import AOS from "aos";
import "aos/dist/aos.css";
import InterimSupport from './page/Interim-support';
AOS.init();
const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

function App() {
  return (
    <>
      <HashRouter>
        <Header />
        <Routes>
          <Route exact path='/' element={< Home />}></Route>
          <Route path='/about' element={< About />}></Route>
          <Route path='/unlocking' element={< Blog />}></Route>
          <Route path='/blog-page' element={< BlogPage />}></Route>
          <Route path='/contact' element={< Contact />}></Route>
          <Route path='/single-blog/:id' element={< SingleBlog />}></Route>
          <Route path='/hone-lab-red' element={< HoneLabRed />}></Route>
          <Route path='/hone-carbon' element={< HoneCarbon />}></Route>
          <Route path='/hone-lab-black' element={< HoneLabBlack />}></Route>
          <Route path='/our-team' element={< Ourteam />}></Route>
          <Route path='/stay-uplated' element={< StayUplated />}></Route>
          <Route path='/contact-us' element={< ContactUs />}></Route>
          <Route path='/hone-support' element={< HoneSupport />}></Route>
          <Route path='/Interim-support' element={< InterimSupport />}></Route>
          <Route path='/documents' element={< Documents />}></Route>
        </Routes>
        <Footer />
      </HashRouter>
    </>
  );
}

export default App;
